import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

// Select
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';

// Snackbar
import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';

// Slider
import Slider from 'rc-slider';
import 'rc-slider/assets/index.css';
import styles from './index.module.scss';

class Navbar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            format: 'hex',
            open: false,
        }
    }

    // æŠŠå­å…ƒä»¶çš„stateå‚³çµ¦çˆ¶å…ƒä»¶ / setSateéåŒæ­¥ï¼Œè¦ç«‹å³å–åˆ°æ­£ç¢ºçš„stateç”¨callback
    handleFormatChange = e => {
        this.setState({ format: e.target.value, open: true }, () => {
            this.props.selectFormat(this.state.format);
        })
    }

    closeSnackbar = () => {
        this.setState({ open: false })
    }

    render() {
        const { level, changeLevel, showAllColors } = this.props;
        const { format, open } = this.state;
        return (
            <header className={styles.navbar}>
                <div className={styles.logo}>
                    <Link to="/">React Color Picker</Link>
                </div>
                {showAllColors && (
                    <div className={styles.sliderContainer}>
                        <span>Level: {level}</span>
                        <div className={styles.slider}>
                            <Slider defaultValue={level} min={100} max={900} step={100} onAfterChange={changeLevel} />
                        </div>
                    </div>
                )}
                <div className={styles.selectContainer}>
                    <Select value={format} onChange={this.handleFormatChange}>
                        <MenuItem value="hex">HEX - #ffffff</MenuItem>
                        <MenuItem value="rgb">RGB - rgb(255,255,255)</MenuItem>
                        <MenuItem value="rgba">RGBA - rgba(255,255,255, 1.0)</MenuItem>
                    </Select>
                </div>
                <Snackbar 
                    anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'left',
                    }}
                    open={open}
                    autoHideDuration={3000}
                    message={<span id="message-id">Format Changed To {format.toUpperCase()}</span>}
                    // ç„¡éšœç¤™ç¶²é 
                    contentPorps={{
                        "aria-describedby": "message-id"
                    }}
                    // æŒ‰ä¸‹ Snackbarä»¥å¤–çš„åœ°æ–¹ é—œé–‰
                    onClose={this.closeSnackbar}
                    action={[
                        // æŒ‰ä¸‹ X é—œé–‰
                        <IconButton key="close" aria-label="Close" color="inherit" onClick={this.closeSnackbar}>
                            <CloseIcon />
                        </IconButton>,
                    ]}
                />
            </header>
        )
    }
}

Navbar.propTypes = {
    level: PropTypes.number,
    changeLevel: PropTypes.func,
    selectFormat: PropTypes.func,
    showAllColors: PropTypes.bool,
};

Navbar.defaultProps = {
    level: 500,
    showAllColors: true,
};

export default Navbar
