import chroma from "chroma-js";
const levels = [50, 100, 200, 300, 400, 500, 600, 700, 800, 900];

// Give any color and return 10 step scale color (black - color - white)
const getRange = hexColor => {
    // set complete white rgb as white
    const white = '#ffffff'
    // custom dark color as black by chroma
    const black = chroma(hexColor).darken(1.4).hex();
    return [
        black,
        hexColor,
        white
    ]
}

const getScale = (hexColor, numberOfColors) => {
    return chroma
        .scale(getRange(hexColor))
        .mode('lab')
        .colors(numberOfColors); // split out to 10 color
}

// Generate level scale to each color
const generatePalette = starterPalette => {
    let newPalette = {
        paletteName: starterPalette.paletteName,
        id: starterPalette.id,
        emoji: starterPalette.emoji,
        colors: {},
    };

    // loop æ¯å€‹ levels å…§çš„ æ·±æ·ºå€¼ï¼ŒæŠŠ
    for(let level of levels) {
        // è¦ç”¢ç”Ÿå¾— newPalette åœ¨ colors object å…§æ–°å¢å±¬æ€§ 10 å€‹ level ç©ºé™£åˆ— (keyå°æ‡‰50, 100, 200...)ï¼Œ
        newPalette.colors[level] = [];
    };

    // loop Palette
    for(let color of starterPalette.colors) {
        let scale = getScale(color.color, 10).reverse(); // æ¯å€‹å–®ä¸€ color å†åˆ‡æˆ10ä»½ï¼Œå»åš(ç°éš) scale åŒ–
        // i æ˜¯ 50, 100, 200, ..., 900
        for(let i in scale) {
            newPalette.colors[levels[i]].push({
                name: `${color.name} ${levels[i]}`,
                id: color.name.toLowerCase().replace(/ /g, "-"), // å–ä»£æ‰€æœ‰ "" æˆ "-"
                hex: scale[i],
                rgb: chroma(scale[i]).css(),
                rgba: chroma(scale[i]).css().replace("rgb","rgba").replace(")",",1.0)")
            })
        }
    };
    return newPalette;
}

export default generatePalette
